  display: inline;
}

.text-spin {
  font-size: 25rem;
  font-weight: 900;
  color: #fec5bb;
}

@media screen and (max-width: 600px) {
  .text-spin {
    font-size: 15rem;
    font-weight: 900;
    color: #fec5bb;
  }

  .mobile-height-crousal {
    height: auto;
  }
}
